import React, { useEffect, useState } from 'react';
import './App.css';

import JSONInput from 'react-json-editor-ajrm';
import locale from 'react-json-editor-ajrm/locale/en';
import 'react-toggle/style.css'; // for ES6 modules
import ReactToggle from 'react-toggle';

function App() {
  const [translations, setTranslations] = useState({
    english: { Loading: '...' },
    german: { Loading: '...' }
  });

  const [currentLang, setCurrentLang] = useState('german');

  function downloadJSON() {
    const obj = translations[currentLang];
    const string = JSON.stringify(obj);
    const a = document.createElement('a');
    a.download = currentLang + '.json';
    a.href = URL.createObjectURL(new Blob([string], {type : "application/json"}))
    a.click();
  }

  useEffect(() => {
    (async () => {
      const german = await fetch('./locales/de_DE.json').then(r => r.json());
      const english = await fetch('./locales/en_US.json').then(r => r.json());
      setTranslations({ english, german });
    })();
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <h1>Antreeb Translation Tool âœ¨</h1>
      </header>

      <div className="sides">
        <div className="lang-toggle">
          <h4>German </h4>
          <ReactToggle
            checked={currentLang === 'english'}
            onChange={v =>
              setCurrentLang(currentLang === 'german' ? 'english' : 'german')
            }
          />
          <h4>English </h4>
          <button onClick={downloadJSON}>Download</button>
        </div>
        <JSONInput
          id="left"
          colors={{
            string: '#DAA520' // overrides theme colors with whatever color value you want
          }}
          locale={locale}
          height="500vh"
          placeholder={translations[currentLang]}
          onChange={ev =>
            setTranslations({ ...translations, [currentLang]: ev.jsObject })
          }
          width="100%"
        />
      </div>
    </div>
  );
}

export default App;
